<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <v-container style="max-width: 80rem">
        <v-layout text-xs-left wrap>
            <v-flex xs12 mb-5>
              <!--context-->
              <!--<v-layout justify-center>-->
                <!--<video width="100%" height="100%" id="player" ref="player" type="video/m3u8" controls="controls"></video>-->
              <!--</v-layout>-->
              <v-layout justify-center>
                <Iplayer id="vueMse" ref="vueMse" width="100%" height="100%" @player="Player = $event"/>
              </v-layout>
              <v-layout class="blue-grey white--text pa-2">
                <v-flex xs2>
                  <v-label>ID: {{this.playerVideoInfo.time}}</v-label>
                </v-flex>
                <v-flex xs2 d-flex>
                  <v-icon small color="white">thumb_up</v-icon>
                  <v-label>{{this.playerVideoInfo.praise}}</v-label>
                </v-flex>
              </v-layout>
              <v-layout class="green lighten-4 pa-2 mt-1">
                <v-flex>
                <h4>title: {{this.playerVideoInfo.title}}</h4>
                <h6>tag:  {{this.playerVideoInfo.tags}}</h6>
                </v-flex>
                <v-flex xs1>
                  <v-tooltip v-model="praiseTip" bottom>
                    <template v-slot:activator="{ on }">
                      <v-btn id="VMPraise" ref="VMPraise" flat icon color="deep-orange" x-large  @click="addPraise">
                        <v-icon class="mr-2">thumb_up</v-icon>
                      </v-btn>
                    </template>
                    <span>{{this.praiseTipText}}</span>
                  </v-tooltip>

                </v-flex>
              </v-layout>
                <v-flex xs12 class="mt-5">
                  <!--discuss-->
                  <v-layout class="mt-3">
                    <v-text-field label="CHAT max 32 byte in english" maxLength="32" hide-details append-icon="edit" v-model="inputDiscuss"/>
                  </v-layout>
                  <v-layout justify-right row class="mt-2">
                    <v-flex xs6 v-if="anonymousShow">
                      <v-text-field ref='VMNickname' label="user nickname max 32 byte in english" maxLength="32" hide-details v-model="inputNickName"/>
                    </v-flex>
                    <v-flex xs6>
                      <v-layout row text-xs-center>
                        <v-btn flat @click="anonymousClick"><v-icon>how_to_reg</v-icon></v-btn>
                        <v-btn class="mt-1 white--text" color="blue-grey" @click="summitChat">summit
                          <v-icon right dark>cloud_upload</v-icon>
                        </v-btn>
                      </v-layout>
                    </v-flex>
                  </v-layout>
                <v-list two-line class="mt-5">
                  <template v-for="(item, index) in this.discussSet">
                    <v-list-tile :key="index" avatar>
                      <v-list-tile-content>
                        <v-list-tile-title >{{ item.context }} </v-list-tile-title>
                        <v-list-tile-sub-title>{{ item.nickname }} {{ item.time}} </v-list-tile-sub-title>
                      </v-list-tile-content>
                    </v-list-tile>
                    <v-divider :key="item.time"></v-divider>
                  </template>
                </v-list>
                </v-flex>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import contractABI from '../js/Ethereum'
import Iplayer from 'xgplayer-vue'
import 'xgplayer-mp4'

export default {
  name: 'Detail',
  components: { Iplayer },
  data: () => ({
    // 变量
    playerVideoInfo: {},
    inputNickName: '',
    inputDiscuss: '',
    anonymousShow: false,
    praiseTip: false,
    praiseTipText: '',
    discussSet: []
  }),
  mounted () {
    // 加载时 接收主参数 初始化视频信息
    if (typeof this.$route.query.key !== 'undefined') {
      let key = this.$route.query.key
      this.playerVideoInfo = JSON.parse(localStorage.getItem(key))

      console.log('mounted', this.playerVideoInfo, this.$store.state.myGateway)

      this.player(this.playerVideoInfo)
    } else {
    // 如果没有token参数就用系统默认token
      alert('未能获得视频hash值')
    }

    // 加载用户名
    this.inputNickName = localStorage.getItem('nickname')

    // 注册后退按键用移动端
    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL)
      window.addEventListener('popstate', this.goBack, false) // false阻止默认事件
    }
  },
  methods: {

    // mediaSource for fmp4
    play2 (videoHash) {
      if (videoHash !== '') {
        const videoControl = this.$refs.player // 需要先设置player控件
        const videoPath = this.$store.state.myGateway + videoHash
        console.log('play2', videoPath)

        var mediaSource = new MediaSource()
        videoControl.src = URL.createObjectURL(mediaSource)

        mediaSource.addEventListener('sourceopen', function () {
          var sourceBuffer = mediaSource.addSourceBuffer('video/mp4; codecs="avc1.42E01E, mp4a.40.2"')

          fetchAB(videoPath, function (buf) {
            sourceBuffer.addEventListener('updateend', function () {
              mediaSource.endOfStream()
              videoControl.play()
            })
            sourceBuffer.appendBuffer(buf)
          })
        })
      }

      function fetchAB (url, cb) {
        let xhr = new XMLHttpRequest()
        xhr.open('get', url)
        xhr.responseType = 'arraybuffer'
        xhr.onload = function () {
          cb(xhr.response)
        }
        xhr.send()
      }
    },

    anonymousClick () {
      if (this.anonymousShow) {
        this.anonymousShow = false
      } else {
        this.anonymousShow = true
      }
    },

    summitChat () {
      if (this.inputDiscuss === '') {
        alert('please input chat content')
        return
      }

      // 检测是否重复发贴
      if (this.discussSet.length > 0) {
        for (let i = 0; i < this.discussSet.length; i++) {
          if (this.discussSet[i].context === this.inputDiscuss) {
            alert('Do not repeat the reply')
            return
          }
        }
      }

      let hashByte = this.playerVideoInfo.videoHash
      let user = this.$store.state.myAccount
      let nickNameByte = ''
      let chat = this.inputDiscuss

      if (this.inputNickName !== null) {
        nickNameByte = this.inputNickName
        // 保存用户名
        localStorage.setItem('nickname', this.inputNickName)
      }
      console.log('summitChat', hashByte, user, nickNameByte, chat)

      let discussItem = {
        'user': user,
        'nickname': nickNameByte,
        'context': chat,
        'praise': '0'
      }
      this.discussSet.unshift(discussItem)

      contractABI.pushDiscuss(hashByte, user, nickNameByte, chat)
    },

    getVideoDiscuss (_videoHash) {
      const hashByte = contractABI.hashToByte32(_videoHash)
      const fileInfoABI = contractABI.initContract(this.$store.state.sysModel)
      let that = this
      fileInfoABI.getVideoDiscussLength(hashByte)
        .then(function (response1) {
          // console.log('discussCount', response1, response1[0].toString())

          let discussCount = parseInt(response1[0].toString())

          if (discussCount > 0) {
            for (let i = 0; i < discussCount; i++) {
              fileInfoABI.getVideoDiscuss(hashByte, i)
                .then(function (response2) {
                  let user = response2[0].toString()
                  let nickname = contractABI.byte32ToStr(response2[1])
                  let context = contractABI.byte32ToStr(response2[2])
                  let praise = response2[3].toString()
                  let time = response2[4].toString()

                  // console.log('res2', response2, i, response2[1].indexOf('000'), '\nreplace', response2[1].substring(0, response2[1].indexOf('000')), response2[1].replace('000', ''), nickname.length, that.discussSet.length)

                  let discussItem = {
                    'user': user,
                    'nickname': nickname,
                    'context': context,
                    'praise': praise,
                    'time': time
                  }
                  that.discussSet.unshift(discussItem)
                  // console.log('getVideoDiscuss', response2, i, user, nickname, context, praise, time, that.discussSet.length)
                })
            }
          }
        })
        .catch(function (error) {
          console.log('getVideoDiscuss error', error)
        })
    },
    // 点赞
    addPraise () {
      // console.log('this.praise', this.praise, this.$store.getters.getHomeVideoList('fdfsf'))
      let praiseKey = 'praise' + this.playerVideoInfo.videoHash
      console.log(localStorage.getItem(praiseKey))
      // 不能重复点赞
      if (localStorage.getItem(praiseKey) === null || localStorage.getItem(praiseKey) === '0') {
        this.praiseTipText = this.playerVideoInfo.praise.toString() + ' + 1'
        this.playerVideoInfo.praise = parseInt(this.playerVideoInfo.praise) + 1
        // 本地记录点赞历史
        localStorage.setItem(praiseKey, '1')
        // 写入区块链
        contractABI.pushPraise(this.playerVideoInfo.videoHash)
      } else {
        this.praiseTipText = 'praised'
      }
      this.praiseTip = true
      let that = this
      // 三秒后关闭提示
      setTimeout(function () {
        that.praiseTip = false
      }, 1000 * 3)
    },
    player (videoinfo) {
      if (videoinfo.videoHash !== '') {
        let playerConfig = {
          id: 'vueMse', // player界面控件
          url: this.$store.state.myGateway + videoinfo.videoHash,
          poster: this.$store.state.myGateway + videoinfo.imgHash,
          videoInit: true, // 在没有视频封面的时候用视频首帧做封面
          autoplay: true, // 页面加载后自动播放
          fluid: true, // 流式布局 随父元素的宽度大小变化
          playsinline: true, // 内联模式 在手机观看时，开启ios和微信的内联模式
          playbackRate: [0.5, 1, 2, 3], // 陪速选项  defaultPlaybackRate: 1.0, // 设置默认陪速
          enterLogo: { // 视频加载页logo [待定]
            url: './static/image/logo-90.png',
            width: 45,
            height: 45
          }
        }

        // console.log('videoControl', playerConfig)
        this.$refs.vueMse.config = playerConfig
        this.getVideoDiscuss(videoinfo.videoHash)
      }
    },
    player3 (videoHash) {
      // video.src = 'http://localhost:8090/ipfs/QmTt4EGNYppgxgqThoSNNXnJRLwCy8SwNnqdfw4DXYrzWe'
      // video.play()
      if (videoHash !== '') {
        const videoControl = this.$refs.player // 需要先设置player控件
        videoControl.src = this.$store.state.myGateway + videoHash
        // console.log('videoControl', videoControl, 'src', videoControl.src, this.$store.state.myGateway + videoHash)
        videoControl.play()

        this.getVideoDiscuss(videoHash)
        // let that = this
        // this.axios.get(this.$store.state.localGateway + videoHash, {emulateJSON: true})
        //   .then(function (response) {
        //     console.log('player', response)
        //     if (response.statusText === 'OK') {
        //       if (videoControl.src === '') {
        //         // videoControl.src = response.data
        //         videoControl.src = that.$store.state.localGateway + videoHash
        //         // console.log('play', that.$store.state.localGateway, vHash, response, videoControl)
        //         videoControl.play()
        //         that.getVideoDiscuss(videoHash)
        //       }
        //     }
        //     // console.log('player hash res', vHash)
        //   })
        //   .catch(function (error) {
        //     console.log('player hash', videoHash, 'error', error)
        //   })
      } else {
        alert('video hash is empty')
      }
    },
    goBack () {
      this.$router.push({'name': 'HelloWorld'})
    }
  },
  // 组件结束执行
  destroyed () {
    console.log('log out')
    // 关闭手机物理按键监听
    window.removeEventListener('popstate', this.goBack, false) // false阻止默认事件
  }

}
</script>

<style scoped>
  .mse {
    width: 600px;
    height: 400px;
    margin: 0px auto;
  }
</style>



