import Vue from 'vue'
import Vuex from 'vuex'
import global from './global'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    mainInfura: 'https://mainnet.infura.io/v3/84101c8a833942018b383e03ca1423f3', // 只读 当没有运行metamask时通过该地址加载主网合约只读数据 让程序可以正常显示
    rinkebyInfura: 'https://rinkeby.infura.io/v3/84101c8a833942018b383e03ca1423f3', // 只读 当没有运行metamask时通过该地址加载测试网只读数据 让程序可以正常显示
    sysAuthor: '0xd5abdd579dd9de11d1431dc5583e4f8d32593458', // 系统默认创作者
    sysTokenSymbol: 'SS', // 只读 当前系统默认token
    contractNetworkByDataFile: '4', // 当前数据文件合约所在网络 1 为主网 4为rinkeby测试网
    myAccount: '', // 我的当前的登录帐号(ethereum)
    myNetwork: '0', // metamask当前登录的网络
    myUploadFileList: [],
    testHash: 'QmWz7HFRrtgiPtASYPxBvCJU3WbpUdpPwRXecx24bAZHVv', // 用于测试的图片文件hash
    testHashStr: 'Qmaisz6NMhDB51cCvNWa1GMS7LU1pAxdF4Ld6Ft9kZEP2a', // 用于测试的文字hash
    gasLimit: 3000000, // 只读
    gasPrice: 2 * 9000000000, // 默认不低于9Gwei
    videoPlayerParam: '',
    sysModel: 'fileInfo', // 当前系统使用的合约 正式文件合约为'fileInfo' 测试合约为'test'
    videoList: [],
    uploadImageHash: '',
    website: 'https://babyhard.com/#',
    ipfsHost: {'server': '104.192.83.208', 'port': '5001'},
    myGateway: 'http://104.192.83.208:8080/ipfs/', // 自定义网关
    publicGateway: 'https://ipfs.jes.xxx/ipfs/', // 当前系统默认公共网关，如果该网关不可用则从公共网关列表中 检测可用网关
    publicGateways: [
      'https://ipfs.jes.xxx/ipfs/',
      'https://ipfs.io/ipfs/',
      'https://gateway.ipfs.io/ipfs/',
      'https://ipfs.infura.io/ipfs/',
      'https://rx14.co.uk/ipfs/',
      'https://ninetailed.ninja/ipfs/',
      'https://upload.global/ipfs/',
      'https://ipfs.globalupload.io/ipfs/',
      'https://catalunya.network/ipfs/',
      'https://siderus.io/ipfs/',
      'https://eu.siderus.io/ipfs/',
      'https://na.siderus.io/ipfs/',
      'https://ap.siderus.io/ipfs/',
      'https://ipfs.eternum.io/ipfs/',
      'https://hardbin.com/ipfs/',
      'https://ipfs.macholibre.org/ipfs/',
      'https://ipfs.works/ipfs/',
      'https://ipfs.wa.hle.rs/ipfs/',
      'https://api.wisdom.sh/ipfs/',
      'https://gateway.blocksec.com/ipfs/',
      'https://ipfs.renehsz.com/ipfs/',
      'https://cloudflare-ipfs.com/ipfs/',
      'https://ipns.co/',
      'https://ipfs.netw0rk.io/ipfs/',
      'https://gateway.swedneck.xyz/ipfs/',
      'https://ipfs.mrh.io/ipfs/',
      'https://gateway.originprotocol.com/ipfs/',
      'https://ipfs.dapps.earth/ipfs/',
      'https://gateway.pinata.cloud/ipfs/',
      'https://ipfs.doolta.com/ipfs/',
      'https://ipfs.sloppyta.co/ipfs/',
      'https://ipfs.busy.org/ipfs/',
      'https://ipfs.greyh.at/ipfs/',
      'https://gateway.serph.network/ipfs/',
      'https://jorropo.ovh/ipfs/',
      'https://gateway.temporal.cloud/ipfs/',
      'https://ipfs.fooock.com/ipfs/',
      'https://ipfstube.erindachtler.me/ipfs/',
      'https://cdn.cwinfo.net/ipfs/'
    ],
    // coinMarket 页面专用数据
    coinMarket: {
      // sscoin当前帐号权益（我的推荐连接，token数量，收益数量eth）
      ssCoinAccount: {'referredLink': '', 'myTokens': '0', 'myDividends': '0'},
      // sscoin合约属性
      ssCoinContract: {
        'totalETH': '0', // 合约现有以太币总量
        'totalToken': '0', // 当前token总量
        'totalDividends': '0', // 当前合约收益总量
        'priceInterval': 0.00000001, // 每个token 价格间隔值 只读固定
        'shareDiviesLowerLimit': '1000000000000000000', // 只读 最小分红金额(1eth)
        'officialReferral': 'https://freeblock.luxe/#/seashells?ss=0xBe1bB799f587249ad8bEf492Cc48E0D5bBB2c991',
        'referredBy': '0x0000000000000000000000000000000000000000', // 默认没有推荐者
        'buyPrice': '0',
        'sellPrice': '0',
        'buyMinimum': '0.02', // 购买最小量
        'sellMinimum': '1' // 卖出最小量
      }
    }
  },
  getters: {
    // 多参数getter 访问this.$store.getters.getHomeVideoList('Param')
    getHomeVideoList: (state) => (pm) => {
      // return localStorage.getItem(key)
      return 'bb' + pm
    },
    // 取随机排除的videoList
    getVideoListByRandom (state) {
      function randomSort () {
        return Math.random() > 0.5 ? -1 : 1 // 通过随机产生0到1的数，然后判断是否大于0.5从而影响排序，产生随机性的效果。
      }
      return state.videoList.sort(randomSort)
    },
    getVideoPlayerParam (state) {
      return state.videoPlayerParam
    }
  },
  mutations: {
    setVideoPlayerParam (state, pm) {
      state.videoPlayerParam = {'index': pm.index, 'time': pm.time, 'videoHash': pm.videoHash, 'imgHash': pm.imgHash, 'author': pm.author, 'category': pm.category, 'title': pm.title, 'tags': pm.tags, 'price': pm.price, 'praise': pm.praise, 'imgByte': pm.imgByte}
    },
    setVideoList (state, pm) {
      if (!global.findVideoInList(pm.videoHash)) {
        state.videoList.push({'index': pm.index, 'time': pm.time, 'videoHash': pm.videoHash, 'imgHash': pm.imgHash, 'author': pm.author, 'category': pm.category, 'title': pm.title, 'tags': pm.tags, 'price': pm.price, 'praise': pm.praise, 'imgPath': pm.imgPath})
      }
      // localStorage.setItem(pm.time, JSON.stringify(pm))
    },
    setMyUploadFileList (state, pm) {
      state.myUploadFileList.push({'videoHash': pm.videoHash, 'imgHash': pm.imgHash, 'author': pm.author, 'category': pm.category, 'title': pm.title, 'tags': pm.tags, 'time': pm.time, 'price': pm.price, 'praise': pm.praise, 'status': pm.status, 'imgPath': pm.imgPath})
      // localStorage.setItem(pm.time, JSON.stringify(pm))
    }
  },
  actions: {

  }
})

export default store



