<template>
              <v-container grid-list-sm fluid style="max-width: 90rem">
                <v-layout row wrap>
                  <v-flex
                    v-for="(item,index) in this.$store.getters.getVideoListByRandom"
                    :key="index"
                    xs12
                    sm4
                    d-flex
                  >
                    <v-hover>
                    <v-card tile class="d-flex ma-2" style="border-radius: 0.5rem" slot-scope="{ hover }"
                            :class="`elevation-${hover ? 12 : 2}`">
                      <!--<v-img-->
                      <!--:src="`https://picsum.photos/500/300?image=${n * 5 + 10}`"-->
                      <!--:lazy-src="`https://picsum.photos/10/6?image=${n * 5 + 10}`"-->
                      <!--aspect-ratio="1"-->
                      <!--class="grey lighten-2"-->
                      <!--/>-->
                      <v-img
                      :src= "`${item.imgPath}`"
                      @click="imgPlayer(index,item)"
                      class="grey lighten-2"
                      >
                        <v-container fill-height fluid class="pa-0" >
                          <v-layout align-end white--text wrap class=" ma-1">
                            <v-layout class="imgText">
                              <v-flex xs10 text-xs-left lexbox>
                                <v-layout row wrap>
                                  <span>{{item.title}}</span>
                                </v-layout>
                                <!--<v-layout row wrap class="mt-1 mb-1" >-->
                                  <!--<span>{{item.tags}}</span>-->
                                <!--</v-layout>-->
                              </v-flex>
                              <v-flex xs2 lexbox>
                                <v-layout>
                                  <v-icon small color="white" class="halfTrans">thumb_up</v-icon>
                                </v-layout>
                                <v-layout justify-rigth row wrap class="mt-1 mb-1">
                                  <span>{{item.praise}}</span>
                                </v-layout>
                              </v-flex>
                            </v-layout>
                          </v-layout>
                        </v-container>
                        <!--<v-card-text>{{item.title}} {{item.tags}}</v-card-text>-->

                      </v-img>
                    </v-card>
                    </v-hover>
                  </v-flex>
                </v-layout>
              </v-container>
</template>

<script>
import contractABI from '../js/Ethereum'
import ipfsApi from 'ipfs-api'

export default {
  name: 'HelloWorld',
  data: () => ({
    testHash: 'QmWz7HFRrtgiPtASYPxBvCJU3WbpUdpPwRXecx24bAZHVv ', // dog jpg
    imgHash: 'QmQtBPVBPHDbviq8aNAa4nheQE8H4dPKpDmeHDPrN3pMNe', // dog jpg
    mp4Hash: 'QmZeM34MDAMCcj7k7Tr29NjoKuwF8xdhHQgV9d1uKC3SK7', // FEF801699F97483308017A9E5C2BD5A2.mp4
    selectPath: '',
    imgShow: true,
    inputStr: ''
  }),
  computed: {
    // getImgByte: function (item) {
    //   return this.videoInfo[item].imgByte
    // }
  },
  mounted () {
    console.log('log in ', this.$options.name)
    // contractABI.initGateway()

    // contractABI.initContract('fileInfo')
    contractABI.initContract(this.$store.state.sysModel)

    // 加载视频图片列表
    contractABI.initVideoList(9999)
  },
  methods: {
    catHash () {
      let hash = this.testHash
      if (this.inputStr !== '') {
        hash = this.inputStr
      }
      const myIpfs1 = ipfsApi('172.245.158.172', '5001', {protocol: 'http'})
      console.log('cat', myIpfs1, hash)

      myIpfs1.cat(hash)
        .then(function (response) {
          // viewControl.src = response.toString() // 显示图片hash :QmS14ULjyvsJz6BtoHoNx8X5GApL3bxfiKgGYp16oDBniC
          console.log('cat hash', hash, '\nsus', response.toString())
        })
        .catch(function (error) {
          console.log('cat hash', hash, 'error', error)
        })
    },
    getHash () {
      if (this.inputStr !== '') {
        let hash = this.inputStr
        const myIpfs = ipfsApi(this.$store.state.ipfsHost.server, this.$store.state.ipfsHost.port, {protocol: 'http'})

        myIpfs.get(hash)
          .then(function (response) {
            console.log('get hash', hash, '\nsus', response)
          })
          .catch(function (error) {
            console.log('get hash', hash, '\nerror', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    player () {
      // video.src = 'http://localhost:8090/ipfs/QmTt4EGNYppgxgqThoSNNXnJRLwCy8SwNnqdfw4DXYrzWe'
      // video.play()

      if (this.inputStr !== '') {
        const myIpfs = ipfsApi(this.$store.state.ipfsHost.server, this.$store.state.ipfsHost.port, {protocol: 'http'})
        const videoControl = this.$refs.player
        let vHash = this.inputStr // 'QmQRX5yb1taEavc48ExrYqZQXCZdqVCmHtQDBcswXmdX7A' t2.mp4
        myIpfs.cat(vHash)
          .then(function (response) {
            videoControl.src = response.toString()
            videoControl.play()
            console.log('player hash res', vHash)
          })
          .catch(function (error) {
            console.log('player hash', vHash, 'error', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    pushToETH () {
      // const obj = 'zdpuAzE1oAAMpsfdoexcJv6PmL9UhE8nddUYGU32R98tzV5fv'
      let _index = 0
      contractABI.pushVideo(this.videoInfo[_index].videoHash, this.videoInfo[_index].imgHash, this.videoInfo[_index].author, this.videoInfo[_index].title, this.videoInfo[_index].tags)
    },
    imgPlayer (index, item) {
      // this.$router.push({path: '/Detail', query: {index: index, time: time}})
      // this.$store.commit('setVideoPlayerParam', item)
      let videoKey = index + item.time
      let videoItem = {'time': item.time.toString(), 'videoHash': item.videoHash, 'imgHash': item.imgHash, 'author': item.author, 'category': item.category, 'title': item.title, 'tags': item.tags, 'price': item.price, 'praise': item.praise.toString()}
      // console.log('go player hash: ', index, videoKey, item, JSON.stringify(videoItem))
      localStorage.setItem(videoKey, JSON.stringify(videoItem))
      let routeData = this.$router.resolve({
        path: '/Detail',
        query: {
          key: videoKey
        }
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },
    goUploadFile () {
      console.log('go uploadFile: ')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/uploadFile'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    }
  }
}
</script>

<style>
  .halfTrans {
    background-color: rgba(0,0,0,0.3);
    border-radius: 1rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.1rem;
  }

  .imgText {
    background: linear-gradient(to bottom, transparent, rgba(0,0,0,0.3));
  }
</style>



