// 初始化公共网站, 如果默认公共网关不在线则从网关列表选择在线网关
function initPublicGateway () {
  axios.get(Store.state.publicGateway + Store.state.testHashStr, {timeout: 5000})
    .then(function (response) {
      console.log('ininGateways', response)
    })
    .catch(function (error) {
      console.log('inin online Gateways', error)
      getOnlinePublicGateway(Store.state.testHashStr)
    })
}
// 从网关列表中设置在线的网关
function getOnlinePublicGateway (testHash) {
  let gateways = Store.state.gateways
  Store.state.gateway = '' // 重置风关
  for (let i = 0; i < gateways.length; i++) {
    if (Store.state.gateway !== '') {
      return
    }
    axios.get(gateways[i] + testHash, {timeout: 3000})
      .then(function (response) {
        // console.log('gatways', gateways[i], response, response.status)

        if (Store.state.gateway === '' && response.statusText === 'OK') {
          Store.state.gateway = gateways[i]
          console.log('onlinegateways', Store.state.gateway)
        }
      })
  }
}

function initContract (contractSymbol) {
  // 检查当前token是否存在
  contractInfo = myAbi.findABIBySymbol(contractSymbol)
  if (typeof contractInfo.abi === 'undefined') {
    alert('not find token' + contractSymbol)
    return
  }

  // 设置数据文件所在网络
  Store.state.contractNetworkByDataFile = contractInfo.host
  let contractProvider
  if (contractInfo.host === '1') {
    contractProvider = new Eth(new Web3.providers.HttpProvider(Store.state.mainInfura))
  } else {
    contractProvider = new Eth(new Web3.providers.HttpProvider(Store.state.rinkebyInfura))
    // contractProvider = new Eth(window.web3.currentProvider)
  }

  let ethContract = new EthContract(contractProvider)

  ContractAbi = ethContract(contractInfo.abi).at(contractInfo.address)

  // console.log('contract', ContractAbi, '\ngetABI', contractInfo.functionHashes, 'contractInfo.address', contractInfo.address)
  return ContractAbi
}

function initMetaMask () {
  // 检查metamask获取当前网络和帐号
  if (typeof window.web3 !== 'undefined') {
    // 检查帐号和网络
    window.web3.version.getNetwork(function (error, response) {
      if (error === null) {
        Store.state.myNetwork = response

        if (typeof window.web3.eth.defaultAccount !== 'undefined') {
          Store.state.myAccount = window.web3.eth.defaultAccount
        }

        console.log('initMetaMask', response, Store.state.myAccount)
      }
    })
  }
}

// 加载我的上传文件用于显示我的上传列表，以当前登录以太坊贴为准
function getMyFileList (myAccount) {
  if (myAccount === '' || typeof myAccount === 'undefined') {
    return
  }
  ContractAbi.getMyVideoLength(myAccount)
    .then(function (response) {
      response = parseInt(response[0].toString())
      console.log('getMyFileList1', myAccount, response)
      for (let i = response - 1; i >= 0; i--) {
        ContractAbi.getMyVideoHashByID(myAccount, i)
          .then(function (response2) {
            let videoHash = response2[0]

            // console.log('getMyFileList2', videoHash, ' id ', i)

            ContractAbi.getVideoInfoByHash(videoHash)
              .then(function (response3) {
                // console.log('getMyFileList3', response3)

                let videoHash = byte32ToHash(response3[0])
                let imgHash = byte32ToHash(response3[1])
                let titleStr = byte32ToStr(response3[2])
                let tagStr = byte32ToStr(response3[3])
                let time = response3[4]
                let praise = response3[5]
                let status = response3[6]

                let videoItem = {
                  'videoHash': videoHash,
                  'imgHash': imgHash,
                  'title': titleStr,
                  'tags': tagStr,
                  'time': time,
                  'praise': praise,
                  'status': status,
                  'imgPath': Store.state.myGateway + imgHash
                }

                // 检查文件是否已加载
                let isFind2 = false
                if (Store.state.myUploadFileList.length > 0) {
                  for (let i3 = 0; i3 < Store.state.myUploadFileList.length; i3++) {
                    if (Store.state.myUploadFileList[i3].videoHash === videoHash) {
                      isFind2 = true
                    }
                  }
                }
                if (isFind2 === false) {
                  Store.commit('setMyUploadFileList', videoItem)
                }

                // console.log('initVideoList', videoHash, imgHash, author, titleStr, tagStr, time)
              })
          })
          .catch(function (error) {
            console.log('error', error)
          })
      }
      // console.log('my upload Video List', Store.state.myUploadFileList)
    })
    .catch(function (error) {
      alert('init video List error\n' + error)
    })
}

// 加载指定数量的视频信息列表
function initVideoList (count) {
  // console.log('web3', this.Web3, '\nethjs-query', this.Eth, '\ncontract', ContractAbi)

  ContractAbi.getVideoCount()
    .then(function (response) {
      // 最大为视频总数
      count = parseInt(count)
      response = parseInt(response[0].toString())
      if (count > response) {
        count = response
      }
      let startIndex = response - count
      console.log('getVideoCount', count, startIndex)
      for (let i = response - 1; i >= startIndex; i--) {
        ContractAbi.getVideoInfoByIndex(i)
          .then(function (response1) {
            let videoHash = byte32ToHash(response1[0])
            let imgHash = byte32ToHash(response1[1])
            let price = byte32ToStr(response1[2])
            let praise = response1[3]
            let titleStr = byte32ToStr(response1[4])
            let tagStr = byte32ToStr(response1[5])
            let time = response1[6]
            let viewCount = response1[7]
            // console.log('initVideoList1', i, videoHash, imgHash, '\ntitle', titleStr, tagStr)

            let videoItem = {
              'videoHash': videoHash,
              'imgHash': imgHash,
              'price': price,
              'praise': praise,
              'title': titleStr,
              'tags': tagStr,
              'time': time,
              'viewCount': viewCount,
              'imgPath': Store.state.myGateway + imgHash
            }

            Store.commit('setVideoList', videoItem)

            // console.log('video image hash', Store.state.localGateway, imgHash, imgByte.length, i.toString())
          })
      }
    })
    .catch(function (error) {
      alert('init video List error\n' + error)
    })
}

function hashToByte32 (fileHex) {
  try {
    let res = hexBase.decode(fileHex).toString('hex').replace(/^1220/, '')
    return ('0x' + res).trim()
  } catch (e) {
    alert('hash to byte error\n' + e)
    return null
  }
}

function byte32ToHash (fileByte32) {
  try {
    let byteBuff = Buffer.from(fileByte32.replace(/^0x/, '1220'), 'hex')
    let res = hexBase.encode(byteBuff)
    return res
  } catch (e) {
    alert('byte to hash error\n' + e)
    return null
  }
}

function strToByte32 (str) {
  try {
    // 字符串长度不大于32
    let strLength = str.length
    if (strLength > 32) {
      strLength = 32
    }
    let res = ethUtil.bufferToHex(ethUtil.setLengthRight(str, strLength))
    // console.log('tobyte', str, res)
    return res.trim()
  } catch (e) {
    alert('string to byte error\n' + e)
    return null
  }
}

function byte32ToStr (byte32) {
  try {
    // let res = ethUtil.setLengthRight(byte32, 32).toString()
    let byteBuffer = ethUtil.toBuffer(byte32)
    let res = byteBuffer.toString('utf-8', 0, byteBuffer.indexOf(0))
    // console.log('byte32ToStr', byte32, res, res2, String.fromCharCode.apply(null, res), URL.createObjectURL(byte32))
    return res
  } catch (e) {
    alert('byte to string error\n' + e)
    return null
  }
}

function pushVideo (_videoHash, _imgHash, _author, _category, _title, _tags, _price, _memo) {
  console.log('pushVideo pm1', _videoHash, _imgHash, _author, _category, _title, _tags, _price, _memo)
  let videoHashByte = hashToByte32(_videoHash)
  if (videoHashByte === null) {
    return
  }

  let imgHashByte = hashToByte32(_imgHash)
  if (imgHashByte === null) {
    return
  }

  let categoryByte = strToByte32(_category)
  if (categoryByte === null) {
    return
  }

  let titleByte = strToByte32(_title)
  if (titleByte === null) {
    return
  }

  let tagByte = strToByte32(_tags)
  if (tagByte === null) {
    return
  }

  let priceByte = strToByte32(_price)
  if (priceByte === null) {
    return
  }

  let memoByte = strToByte32(_memo)
  if (memoByte === null) {
    return
  }

  // let statusUint = parseInt(_status)
  console.log('pushVideo pm2', videoHashByte, imgHashByte, _author, categoryByte, titleByte, tagByte, priceByte, memoByte)
  // 16进制转换 ethereumjsAbi.rawEncode(['uint', 'string', 'string'], [3, 'a', 'b']).toString('hex')/
  let paramsHex = ethereumjsAbi.rawEncode(['bytes32', 'bytes32', 'address', 'bytes32', 'bytes32', 'bytes32', 'bytes32', 'bytes32'], [videoHashByte, imgHashByte, _author, categoryByte, titleByte, tagByte, priceByte, memoByte]).toString('hex')

  let privateKey = Buffer.from(CommunityFundKey, 'hex')

  // 获取随机数web3js.eth.getTransactionCount(addressFrom).toString(16)
  // 已步获得随机数
  web3js.eth.getTransactionCount(CommunityFund, function (error, result) {
    if (!error) {
      // console.log('conaddress', contractInfo.address)
      let rawTx = {
        nonce: result, // 随机数
        gasPrice: web3js.toHex(Store.state.gasPrice),
        gasLimit: web3js.toHex(Store.state.gasLimit),
        to: contractInfo.address, // 接受方地址或者合约地址
        data: '0x' + '82d6aa68' + paramsHex// 合约参数
      }
      // 使用私钥对原始的交易信息进行签名，得到签名后的交易数据
      let tx = new Tx(rawTx)
      tx.sign(privateKey)
      let serializedTx = tx.serialize()
      web3js.eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
        if (!err) {
          alert('upload file is pending \nsend hash: ' + hash + '\nvideo hash: ' + _videoHash)
          console.log('send hash:', hash, '\nvideo hash: ', _videoHash)
        } else {
          alert('send error:\n' + err)
          console.log('send error:', err)
        }
      })
    } else {
      alert('getTransactionCount\n' + error)
    }
  })

  // pushMyVideo(_author, videoHashByte)
}

function pushDiscuss (_videoHash, _user, _nickName, _chat) {
  let videoHashByte = hashToByte32(_videoHash)
  if (videoHashByte === null) {
    return
  }

  let nickNameByte = strToByte32(_nickName)
  if (nickNameByte === null) {
    return
  }

  let chatByte = strToByte32(_chat)
  if (chatByte === null) {
    return
  }

  // 16进制转换 ethereumjsAbi.rawEncode(['uint', 'string', 'string'], [3, 'a', 'b']).toString('hex')/
  let paramsHex = ethereumjsAbi.rawEncode(['bytes32', 'address', 'bytes32', 'bytes32'], [videoHashByte, _user, nickNameByte, chatByte]).toString('hex')
  console.log('prm', _videoHash, _user, _nickName, _chat)

  let privateKey = Buffer.from(CommunityFundKey, 'hex')

  // 获取随机数web3js.eth.getTransactionCount(addressFrom).toString(16)
  // 已步获得随机数
  web3js.eth.getTransactionCount(CommunityFund, function (error, result) {
    if (!error) {
      // console.log('conaddress', contractInfo.address)
      let rawTx = {
        nonce: result, // 随机数
        gasPrice: web3js.toHex(Store.state.gasPrice),
        gasLimit: web3js.toHex(Store.state.gasLimit),
        to: contractInfo.address, // 接受方地址或者合约地址
        data: '0x' + '9406f61e' + paramsHex// 合约参数
      }
      // 使用私钥对原始的交易信息进行签名，得到签名后的交易数据
      let tx = new Tx(rawTx)
      tx.sign(privateKey)
      let serializedTx = tx.serialize()
      web3js.eth.sendRawTransaction('0x' + serializedTx.toString('hex'), function (err, hash) {
        if (!err) {
          // alert('chat hash:' + hash + '\nvideo hash:' + _videoHash)
          console.log('pushDiscuss hash:', hash, '\nvideo hash:', _videoHash)
        } else {
          // alert('send error:\n' + err)
          console.log('pushDiscuss error:', err)
        }
      })
    } else {
      alert('pushDiscuss getTransactionCount error\n' + error)
    }
  })
}

