<script>
import videojs from 'video.js'
import ethContract from '../js/Ethereum'
import ipfsApi from 'ipfs-api'

export default {
  name: 'uploadFile',
  data: () => ({
    // 变量
    inputTitle: '',
    inputTag: '',
    tagArray: [],
    inputMyAccount: '',
    inputCost: '0',
    selectVideoPath: '',
    barVal: 0,
    num: 1,
    myPlayer: '',
    waitDialog: false,
    showVideo: true,
    showVideoPoster: true
  }),
  mounted () {
    this.clearPageMeta()
    // 加载时初始化智能合约
    ethContract.initContract(this.$store.state.sysModel)

    // 加载我的文件上传列表
    this.loadMyFile()
  },
  watch: {
    // 监视字符长度
    tagArray: function () {
      let alength = 0
      for (let i = 0; i < this.tagArray.length; i++) {
        alength += this.tagArray[i].length
      }
      // console.log('tagArray length', alength)
      if (alength < 32) {
        this.inputTag = this.tagArray
      }
    }
  },
  computed: {
    // getImgByte: function (item) {
    //   return this.videoInfo[item].imgByte
    // }
  },
  filters: {
    // 用于条件显示文本插值

    // 上传列表
    myVideoShowStatus (status) {
      let res = 'normal' // 默认值1时显示
      if (status === 'pending') {
        res = 'pending'
      }
      if (status === 0) {
        res = 'disabled'
      }
      return res
    },

    // 上传视频显示开关文本
    hideShowText (status) {
      let res = 'hide' // 默认值1时显示
      if (!status) {
        res = 'show'
      }
      return res
    }
  },
  methods: {
    // 方法合集
    showVideoPosterSwitch () {
      if (this.showVideoPoster) {
        this.showVideoPoster = false
      } else {
        this.showVideoPoster = true
      }
    },
    showVideoSwitch () {
      if (this.showVideo) {
        this.showVideo = false
      } else {
        this.showVideo = true
      }
    },

    initVideo (src) {
      // 初始化视频方法
      let vmPlayer = this.$refs.player
      this.myPlayer = videojs(vmPlayer, {
        // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        // 自动播放属性,muted:静音播放
        autoplay: 'muted',
        // 建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: 'auto'
      })

      // myPlayer.src({src: '../../static/video/test.mp4', type: 'video/mp4'})
      this.myPlayer.src({src: src, type: 'video/mp4'})
      console.log('initVideo', this.myPlayer.src())
      this.myPlayer.play()
    },
    getMetaMaskAccount () {
      // 检查metamask获取当前网络和帐号
      if (typeof window.web3 !== 'undefined') {
        // 检查帐号和网络
        let that = this
        // console.log('ccounts', that.$store.state.contractNetworkByDataFile, window.web3, that.$store.state.myNetwork)
        window.web3.version.getNetwork(function (error, response) {
          if (error === null) {
            if (response === that.$store.state.contractNetworkByDataFile) {
              if (typeof window.web3.eth.defaultAccount !== 'undefined') {
                that.inputMyAccount = window.web3.eth.defaultAccount
              } else {
                alert('please login MetaMask')
              }
            } else {
              let networkHint = 'Main'
              if (that.$store.state.contractNetworkByDataFile === '4') {
                networkHint = 'Rinkeby'
              }
              alert('please select MetaMask network in ' + networkHint)
            }
            // console.log('response', response, window.web3.eth.defaultAccount, Store.state.myAccount)
          }
        })
      } else {
        alert('not MetaMask in your machine')
      }
    },
    clearPageMeta () {
      // 清理页面数据
      if (this.$store.state.myUploadFileList.length > 0) {
        this.$store.state.myUploadFileList.slice(0, this.$store.state.myUploadFileList.length)
      }
    },
    goTest () {
      console.log('go test: ')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/test'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },

    temp () {
      this.$global.findMyUploadFileList2('QmfQs7cQ6BKoLeSi9KnjcQBMAzNcCfhXkMWcZPRqpjC3eW')
    },
    loadMyFile () {
      // 检查metamask获取当前网络和帐号
      if (typeof window.web3 !== 'undefined') {
        // 检查帐号和网络
        let that = this
        window.web3.version.getNetwork(function (error, response) {
          if (error === null) {
            console.log('initMetaMask', response, that.inputMyAccount, that.$store.state.contractNetworkByDataFile)
            that.$store.state.myNetwork = response
            if (that.$store.state.contractNetworkByDataFile === response) {
              if (typeof window.web3.eth.defaultAccount !== 'undefined') {
                console.log('initMetaMask2', response, that.inputMyAccount)
                that.$store.state.myAccount = window.web3.eth.defaultAccount
                that.inputMyAccount = that.$store.state.myAccount

                ethContract.getMyFileList(that.inputMyAccount)
              }
            }
          }
        })
      }
    },

    upLoadFile () {
      const selectVideo = this.$refs.selectVideo
      const videoImg = this.$refs.videoImg
      // console.log('select file', videoImg, selectVideo.files[0])
      // selectVideoImg.value 路径；selectVideoImg.files[0].name文件名
      if (typeof videoImg.src === 'undefined') {
        alert('please select video img file')
        return
      }

      if (typeof selectVideo.files[0] === 'undefined') {
        alert('please select video file')
        return
      }

      if (this.inputTitle === '') {
        alert('please input video title')
        return
      }

      if (this.inputTag === '') {
        alert('please input video tag')
        return
      }

      if (this.inputMyAccount.length !== 0 && this.inputMyAccount.length !== 42) {
        alert('please input valid ethereum account')
        return
      }

      let videoFile = selectVideo.files[0]

      // 判断浏览器是否支持 FileReader
      if (!window.FileReader) {
        alert('您的设备不支持图片预览功能，如需该功能请升级您的设备！')
        return
      }

      // 检测是否重复提交
      if (this.$store.state.myUploadFileList.length > 0) {
        for (let i = 0; i < this.$store.state.myUploadFileList.length; i++) {
          if (this.$store.state.myUploadFileList[i].videoFile === videoFile) {
            alert('Do not repeat the upload file')
            return
          }
        }
      }

      // 上传视频
      let that = this
      that.waitDialog = true // 显示等待框
      let videoReader = new FileReader()

      // reader.readAsDataURL(uploadFile)
      videoReader.readAsArrayBuffer(videoFile)
      videoReader.onload = function () {
        let videoBuffer = Buffer.from(new Uint8Array(videoReader.result))

        console.log('upLoad File buffer length', videoBuffer.length, that.$store.state.ipfsHost.server)

        if (videoBuffer.length > 1) {
          const myIpfs = ipfsApi(that.$store.state.ipfsHost.server, that.$store.state.ipfsHost.port)

          myIpfs.add(videoBuffer)
            .then(function (response2) {
              let videoHash = response2[0].hash
              console.log('videoBuffer', response2)
              // 上传视频图片
              let imgBlob = that.base64ToBlob(videoImg.src)
              let videoImgReader = new FileReader()
              videoImgReader.readAsArrayBuffer(imgBlob)
              videoImgReader.onload = function () {
                let videoImgBuffer = Buffer.from(new Uint8Array(videoImgReader.result))
                myIpfs.add(videoImgBuffer)
                  .then(function (response3) {
                    let imgHash = response3[0].hash

                    console.log('videoImgBuffer', response3)
                    let author = that.inputMyAccount
                    if (author === '' || typeof author === 'undefined') {
                      author = that.$store.state.sysAuthor
                    }

                    // 视频属性写入ETH
                    let fileItem = {
                      'videoFile': videoFile,
                      'videoHash': videoHash,
                      'imgHash': imgHash,
                      'author': author,
                      'category': '1:video', // 按josn格式定义多属性（1为视频大类）
                      'title': that.inputTitle,
                      'tags': that.inputTag,
                      'price': '1:vip,2:usd,3:0.35', //  视频价格待定义 按josn格式定义3个属性(1视频类型，2基本货币单位，3价格)
                      'memo': '',
                      'status': 'pending',
                      'imgPath': that.$store.state.myGateway + imgHash
                    }

                    console.log('upload video hash', fileItem.videoHash, '\nimgHash', fileItem.imgHash, '\nauthor', that.inputMyAccount, that.inputTitle, that.inputTag)

                    // ethContract.pushVideo(videoHash, videoImgHash, that.$store.state.sysAuthor, _category, that.inputTitle, that.inputTag, _price)
                    ethContract.pushVideo(fileItem.videoHash, fileItem.imgHash, fileItem.author, fileItem.category, fileItem.title, fileItem.tags, fileItem.price, fileItem.memo)
                    that.test3(fileItem.videoHash, fileItem.imgHash)
                    that.$store.state.myUploadFileList.unshift(fileItem)
                    that.waitDialog = false // 消失等待框
                  })
                  .catch(function (error) {
                    console.log('upload video img error', error)
                    that.waitDialog = false // 消失等待框
                  })
              }
            })
            .catch(function (error) {
              console.log('upload video error ', error)
              that.waitDialog = false // 消失等待框
            })
        } else {
          alert('not buffer content')
          that.waitDialog = false // 消失等待框
        }
      }
    },

    // 普通视频文件小于10m（不分片）
    selectVideo (e) {
      let selectFile = e.target

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        alert('您的设备不支持图片预览功能，如需该功能请升级您的设备！')
        return
      }
      // 检查文件大小不超50m
      if ((selectFile.files[0].size / 1024) > (1024 * 50)) {
        alert('file max size 50M')
        return
      }
      let reader = new FileReader()
      // 设置文件
      // console.log('selectFile', selectFile, '\nreader', reader, '\npath', selectFile.value, selectFile.files[0].name)
      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        this.waitDialog = true
        let vmPlayer = this.$refs.player
        let vmVideoImg = this.$refs.videoImg

        reader.readAsDataURL(selectFile.files[0])
        let that = this
        reader.onload = function () {
          // 处理读取的结果
          // let buffer = Buffer.from(reader.result)
          // player.src = buffer

          vmPlayer.src = reader.result
          that.waitDialog = false
          // console.log('fdsf', vmPlayer.videoHeight)
        }
        // 截取视频第一帧为视频封面
        vmPlayer.addEventListener('loadeddata', function captureImage () {
          if (vmPlayer.videoHeight < 360) {
            alert('The video resolution is too low, than more than 360')
          }

          let canvas = document.createElement('canvas')
          // 计算视频封面缩放比例 标准为h480
          let scaling = Math.round((480 / vmPlayer.videoHeight) * 100) / 100 // 四舍五入保留两位小数，截取小数位用toFixed(2)
          canvas.width = vmPlayer.videoWidth * scaling
          canvas.height = vmPlayer.videoHeight * scaling

          console.log('canvas', vmPlayer.videoWidth, vmPlayer.videoHeight, scaling, canvas.width, canvas.height)
          canvas.getContext('2d').drawImage(this, 0, 0, canvas.width, canvas.height)

          // 设置动态添加的元素的样式
          // viewImg.className = 'all'
          // viewImg.crossOrigin = 'anonymous'
          // alert(canvas.toDataURL("image/png"))
          let canvasSrc = canvas.toDataURL('image/jpeg')
          // let canvasBuffer = Buffer.from(canvasSrc)
          // vmVideoImg.src = canvas.toDataURL('image/jpg')
          // console.log('canvasSrc', Buffer.from(canvasSrc))
          vmVideoImg.src = canvasSrc
          that.selectVideoPath = e.target.value
          that.inputTitle = selectFile.files[0].name.substring(0, selectFile.files[0].name.length - 4).substring(0, 32).trim()
        })
      }
    },
    // 将base64转换为blob
    base64ToBlob (dataurl) {
      let arr = dataurl.split(',')
      let mime = arr[0].match(/:(.*?);/)[1]
      let bstr = atob(arr[1])
      let n = bstr.length
      let u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], {type: mime})
    },

    // 将blob转换为file
    blobToFile (theBlob, fileName) {
      theBlob.lastModifiedDate = new Date()
      theBlob.name = fileName
      return theBlob
    },
    test6 () {
      // this.$router.push({path: '/cuplayer'})
      let routeData = this.$router.resolve({
        path: '/cuplayer'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },
    // 直接上传图片至服务器
    test5 (file) {
      // const file = this.$refs.files[0]
      const fd = new FormData()
      fd.append('file', file)
      this.axios.defaults.withCredentials = true
      this.axios({
        method: 'post',
        emulateJSON: true,
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        url: 'http://192.74.254.74/static/',
        data: fd
      }).then(function (response) {
        if (response.data.code === '200') {
          console.log(response)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },

    test4 () {
      let videoImg = this.$refs.videoImg
      // imgss.src = this.$store.state.gateway + 'QmZ4WUeQ8yjvZBRLF94CP4Kpc3AebiAGQdbJxyG3kUK8Va'
      // imgss.src = 'https://ipfs.jes.xxx/ipfs/QmWz7HFRrtgiPtASYPxBvCJU3WbpUdpPwRXecx24bAZHVv'
      // http://192.74.254.74:8080/ipfs/QmVh1g359Sb2YNmegSxSRp5paktWGVLQMwtgYBx1haqwjA

      // let hash = 'QmVh1g359Sb2YNmegSxSRp5paktWGVLQMwtgYBx1haqwjA'
      const myIpfs = ipfsApi(this.$store.state.ipfsHost, this.$store.state.ipfsHost.port, {protocol: 'http'})
      let videoImgBuffer = Buffer.from(videoImg.src)
      console.log('test4', this.$store.state.ipfsHost.server, myIpfs, videoImgBuffer)
      let that = this
      myIpfs.add(videoImgBuffer)
        .then(function (response1) {
          that.$store.state.uploadImageHash = response1[0].hash
          console.log('upLoadFile img hash', that.$store.state.uploadImageHash)
        })
        .catch(function (error) {
          console.log('upload video img error', error)
        })
      // imgss.src = this.$store.state.localGateway + 'Qma4XVXaEv237FXkHzqyU84MViqu7ekKzkM3FQrVin2t5S'
    },
    test3 (videoHash, imgHash) {
      let videohashtobyte = ethContract.hashToByte32(videoHash)
      let imghashtobyte = ethContract.hashToByte32(imgHash)
      let bytetohash = ethContract.byte32ToHash(videohashtobyte)
      let bytetostr = ethContract.byte32ToStr(videohashtobyte)
      let test2 = ethContract.strToByte32('1:vip,2:usd,3:0.35')
      let test3 = ethContract.strToByte32('')

      console.log('test3', videoHash, videohashtobyte, '\n', imgHash, imghashtobyte, '\n', bytetohash, bytetostr, test2, test3)
    },
    selectImg (e) {
      let selectFile = e.target

      if (this.$global.verificationFileSize(selectFile, 100) === '0') {
        return
      }

      // 判断浏览器是否支持 FileReader
      if (!e || !window.FileReader) {
        alert('您的设备不支持图片预览功能，如需该功能请升级您的设备！')
        return
      }
      let reader = new FileReader()
      // 设置文件

      // 这里是最关键的一步，转换就在这里
      if (selectFile.files[0]) {
        // console.log('selectFile', selectFile, '\npath', selectFile.value, selectFile.files[0].name, reader.result)

        this.$global.verificationPicDimension(selectFile, 640, 360)

        let viewImg = this.$refs.videoImg

        reader.onload = function (e) {
          let imgData = e.target.result

          viewImg.src = imgData

          // console.log('img', imgData.naturalWidth, imgData.width, viewImg.scrollWidth, viewImg.clientWidth)
        }

        reader.readAsDataURL(selectFile.files[0])
      }
    },
    fileReaderToBuffer (bufferString) {
      let fileSize = bufferString.length
      this.num = 1
      const blockSize = 1 * 1024 * 1024
      let nextSize = Math.min(this.num * blockSize, fileSize)

      let buffer = Buffer.from(bufferString)
      console.log('\nreader selectImg', fileSize, nextSize, buffer)

      let fileBuffer = ''
      // 大于1M，分段上传
      while (fileSize > nextSize) {
        nextSize = Math.min(this.num * blockSize, fileSize)
        const slice = bufferString.slice((this.num - 1) * blockSize, nextSize)
        // const percent = (blockSize * this.num) / fileSize
        // 上传出错则重新上传
        try {
          fileBuffer += Buffer.from(slice)
          // console.log('splitFile2', fileBuffer, nextSize, this.num)
          // response = await this.onUpload2(slice, file, md5code, percent)
        } catch (error) {
          // response = await this.onUpload2(slice, file, md5code, percent)
        }
        this.num++
      }

      return fileBuffer
    }
  },
  beforeDestroy () {
    // 组件结束前执行
  }
}
</script>

