<template>
  <v-app>
    <v-toolbar app class="white">
      <v-toolbar-title class="text-uppercase">
        <v-layout row>
        <v-img height="2rem" width="2rem" src="./static/image/logo-90.png"/>
        <span class="ml-1" style="cursor: pointer" @click="goHome" >babyhard.com</span>
        </v-layout>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn flat @click="goXXCoin">
        <span class="mr-1">SS Coin</span>
      </v-btn>
      <v-btn flat @click="goUploadFile">
        <span class="mr-2">Upload Video</span>
      </v-btn>
    </v-toolbar>

    <!--<v-content class="pa-0 white" style="background-image: url(../static/image/nature-3054797_1920.jpg)">-->
    <v-content class="pa-0 white">
    <router-view :key="$router.fullPath"/>
      <!--缓冲页面用于在移动端按返回键可以回到前一页 不刷新-->
      <!--<keep-alive>-->
        <!--<router-view v-if="$route.meta.keepAlive">-->
          <!--&lt;!&ndash; 这里是会被缓存的视图组件，比如 page1,page2 &ndash;&gt;-->
          <!--<HelloWorld/>-->
        <!--</router-view>-->
      <!--</keep-alive>-->

      <!--<router-view v-if="!$route.meta.keepAlive">-->
        <!--&lt;!&ndash; 这里是不被缓存的视图组件，比如 page3 &ndash;&gt;-->
        <!--<Detail/>-->
      <!--</router-view>-->

    </v-content>
    <!--分割线-->
    <v-divider></v-divider>
    <v-footer class="pa-3">
      <v-layout
        justify-center
        row
      >
        <span>
          <v-layout row>
            <v-img height="2rem" width="2rem" src="./static/image/logo-90.png"/>
            <span class="mt-1 ml-2">&copy; {{ new Date().getFullYear() }}-babyhard.com</span>
            <a href="https://github.com/babyhard/video">
              <v-img height="1.5rem" width="1.5rem" class="ml-3 mt-1" src="./static/image/githubico.jpg"/>
            </a>
          </v-layout>
        </span>

      </v-layout>
    </v-footer>
  </v-app>
  <!--<div id="app">-->
    <!--<img src="./assets/logo.png">-->
    <!--<router-view/>-->
  <!--</div>-->
</template>

<script>
import HelloWorld from './components/HelloWorld'
import Detail from './components/Detail'
export default {
  name: 'App',
  components: {Detail, HelloWorld},
  methods: {
    goHome () {
      this.$router.push({path: '/'})
    },
    goUploadFile () {
      console.log('go uploadFile:')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/uploadFile'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    },
    goXXCoin () {
      console.log('go xxCoin')
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})
      let routeData = this.$router.resolve({
        path: '/coinMarket'
      })
      window.open(routeData.href, '_blank') // 新窗口打开
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



